@font-face {
  src: url("./ubuntu-300.woff") format("woff"),
    url("./ubuntu-300.woff2") format("woff2");
  font-family: "Ubuntu";
  font-weight: 400;
}

@font-face {
  src: url("./ubuntu-500.woff") format("woff"),
    url("./ubuntu-500.woff2") format("woff2");
  font-family: "Ubuntu";
  font-weight: 500;
}
@font-face {
  src: url("./ubuntu-500italic.woff") format("woff"),
    url("./ubuntu-500italic.woff2") format("woff2");
  font-family: "Ubuntu Italic";
  font-weight: 500;
}
@font-face {
  src: url("./ubuntu-700.woff") format("woff"),
    url("./ubuntu-700.woff2") format("woff2");
  font-family: "Ubuntu";
  font-weight: 700;
}



